<template>
  <div v-if="playingSectionData && playingAlbumData" class="container-play-music">
    <div class="music-title">
      {{ playingSectionData.title }}
    </div>

    <div
      :style="{ 'background-image': `url( ${playingSectionData.img ? playingSectionData.img : playingAlbumData.cover} )` }"
      class="music-cover"
    />
    
    <div class="top-button f-i-c">
      <div class="every-button f-col-between-c">
        <img src="@/assets/images/play_music_share.png" alt="">
        <p>转发</p>
      </div>
      <div class="every-button">
        <div
          class="count-down-btn f-col-between-c"
          @click="isShowCountDownSheets = true"
        >
          <img src="@/assets/images/play_music_timer.png" alt="">
          <p>定时</p>
        </div>
        <!-- <div v-else class="count-down f-col-between-c">
          倒计时
          <van-count-down
            :time="countDownTime"
            @finish="countDownFinish"
          />
        </div> -->
      </div>
      <div class="every-button f-col-between-c">
        <img
          @click="isShowSpeedSheets = true"
          :src="speedIcons[audio.playbackRate]"
        >
        <p>倍速</p>
      </div>
    </div>

    <PlayMusicProgress class="play-music-progress-box" />

    <div class="control flx">
      <div @click="showMusicList" class="control-side">
        <img src="@/assets/images/play_music_list.png" alt="">
        <p>列表</p>
      </div>
      <div class="control-middle f-row-between-c">
        <img @click="lastMusic" class="side" src="@/assets/images/play_music_last.png" alt="">
        <img
          @click="clickPlayOrPauseButton"
          :src="playStatus ? switchIcons.pauseIcon : switchIcons.playIcon"
          class="middle"
        >
        <img @click="nextMusic" class="side" src="@/assets/images/play_music_next.png" alt="">
      </div>
      <div @click="showTextDesc" class="control-side">
        <img src="@/assets/images/play_music_text.png" alt="">
        <p>文稿</p>
      </div>
    </div>

    <div @click="clickGetIntoAlbum" class="inlet flx">
      <div class="cover">
        <img
          :src="playingSectionData.img ? playingSectionData.img : playingAlbumData.cover"
          alt=""
        >
      </div>
      <div class="content f-row-between">
        <div class="left">
          <p class="first-title one-ellipsis">
            {{ playingAlbumData.name }}
          </p>
          <p class="second-title one-ellipsis">
            {{ playingAlbumData.intro }}
          </p>
        </div>
        <div class="right f-row-between-c">
          <p>
            进入专辑
          </p>
          <img src="@/assets/images/play_music_more.png" alt="">
        </div>
      </div>
    </div>

    <div class="split-1" />

    <div class="message-title">
      <AlbumRowTitle rowTitle="精彩留言" />
    </div>

    <div class="message-list-box">
      <MessageList
        :messageList="message.list"
        :isShowCommentLink="false"
        @switchPraise="switchPraise"
      />
      <div
        @click="toTotalMessagePage"
        v-if="message.len > 5"
        class="see-more-message f-c"
      >
        查看更多留言
      </div>
    </div>

    <WriteMessageBox
      @click="clickWriteMessageBox"
      :class="['write-message-box', isShowTextDesc ? 'z-index-3000' : '']"
    />

    <van-overlay
      @click="clickMessageOverlay"
      :show="isShowWriteBox"
    >
      <WriteMessage
        v-if="isShowWriteBox"
        @sendMessage="sendMessage"
        class="write-message"
      />
    </van-overlay>

    <van-popup
      v-model="isShowTextDesc"
      position="right"
      :style="{ height: '100%', width: '100%' }"
      closeable
      @close="closeTextPopup"
      close-icon-position="top-left"
    >
      <div class="text-desc">
        <p class="title">
          {{ playingSectionData.title }}
        </p>
        <div
          v-html="playingSectionData.desc"
          class="content"
        />

        <div class="middle-block-1" />

        <div class="message-title">
          <AlbumRowTitle rowTitle="精彩留言" />
        </div>
        <div class="message-list-box">
          <MessageList
            :messageList="message.list"
            :isShowCommentLink="false"
            @switchPraise="switchPraise"
          />
          <div
            @click="toTotalMessagePage"
            v-if="message.len > 5"
            class="see-more-message f-c"
          >
            查看更多留言
          </div>
        </div>

        <div class="bottom-desc" />
      </div>
    </van-popup>

    <van-popup
      v-model="isShowMusicList"
      round
      position="bottom"
      :style="{ height: '75%', 'overflow-y': 'hidden' }"
    >
      <div class="music-list-popup-box">
        <div class="head-area f-row-between-c">
          <p class="title">
            目录
          </p>
          <div @click="changeListOrder" class="order f-row-between-c">
            <img :src="listOrder === true ? sortPositive : sortReverse" alt="">
            <p>
              {{ listOrder ? '正序' : '倒序' }}
            </p>
          </div>
        </div>
        
        <div class="music-list-outer">
          <div
            v-for="(item, itemIndex) in musicList"
            :key="item._id"
          >
            <MusicList
              :albumDetail="playingAlbumData"
              :section="item"
              :sectionIndex="itemIndex"
              @click="clickMusicList"
              class="music-list-box"
            />
          </div>
        </div>
      </div>
    </van-popup>

    <!-- 倍速上拉菜单 -->
    <van-action-sheet
      v-model="isShowSpeedSheets"
      :actions="speedActions"
      @select="selectSpeed"
    />

    <!-- 倒计时上拉菜单 -->
    <van-action-sheet
      v-model="isShowCountDownSheets"
      :actions="countDownActions"
      @select="selectCountDown"
    />

    <div class="empty-block" />
  </div>
</template>

<script>
import { mapState } from 'vuex';
import PlayMusicProgress from '@/components/playMusic/PlayMusicProgress'
import MessageList from '@/components/message/MessageList';
import WriteMessageBox from '@/components/message/WriteMessageBox';
import WriteMessage from '@/components/message/WriteMessage';
import MusicList from '@/components/musicList/MusicList';
import AlbumRowTitle from '@/components/album/AlbumRowTitle.vue';
import { toAlbumPage } from '@/utils/proxyRouteSkip';
import { apiAlbumItemPlay, apiAlbumItemDetail, apiAlbumDetail } from '@/service/api/program';
import { apiLeaveMessageListBySection } from '../../service/api/leaveMessage';
import { getAlbumType } from '@/utils/constant';

import Vue from 'vue';
import { Popup, Overlay, ActionSheet, CountDown, Toast } from 'vant';

Vue.use(Popup).use(Overlay).use(ActionSheet).use(CountDown).use(Toast);

export default {
  name: 'play-music',

  components: {
    PlayMusicProgress,
    MessageList,
    WriteMessageBox,
    MusicList,
    AlbumRowTitle,
    WriteMessage
  },

  data() {
    return {
      isShowTextDesc: false,
      isShowMusicList: false,
      isShowWriteBox: false,
      clientHeight: 0,
      switchIcons: {
        pauseIcon: require('@/assets/images/play_music_pause.png'),
        playIcon: require('@/assets/images/play_music_play.png')
      },
      speedIcons: {
        '1': require('@/assets/images/speed_1x.png'),
        '1.25': require('@/assets/images/speed_1.25x.png'),
        '1.5': require('@/assets/images/speed_1.5x.png'),
        '2': require('@/assets/images/speed_2x.png'),
        '2.5': require('@/assets/images/speed_2.5x.png'),
        '3': require('@/assets/images/speed_3x.png'),
      },
      isShowSpeedSheets: false,
      speedActions: [
        { name: '1x', speed: 1 },
        { name: '1.25x', speed: 1.25 },
        { name: '1.5x', speed: 1.5 },
        { name: '2x', speed: 2 },
        { name: '2.5x', speed: 2.5 },
        { name: '3x', speed: 3 },
      ],
      countDownTime: 0,
      isShowCountDownSheets: false,
      countDownActions: [
        { name: '10分钟后', countType: 'time', time: 10 * 60 * 1000 },
        { name: '30分钟后', countType: 'time', time: 30 * 60 * 1000 },
        { name: '60分钟后', countType: 'time', time: 60 * 60 * 1000 },
        { name: '播完当前', countType: 'number', sectionCounts: 1 },
        { name: '播完3节', countType: 'number', sectionCounts: 3 },
        { name: '播完5节', countType: 'number', sectionCounts: 5 },
      ],

      albumId: null,
      sectionId: null,

      sortPositive: require('../../assets/images/play_music_positive.png'),
      sortReverse: require('../../assets/images/play_music_reverse.png'),
      
      listOrder: true, // ? true: 正序  false: 倒序
      musicList: null,
      positiveList: null,
      reverseList: null,

      message: {
        list: [],
        pageNum: 0,
        pageSize: 6,
        len: 0
      }
    }
  },

  computed: mapState({
    audio: state => state.audioState.audio,
    playStatus: state => state.audioState.playStatus,
    playbackRate: state => state.audioState.playbackRate,
    playingAlbumData: state => state.playingAlbumData,
    playingAlbumId: state => state.playingAlbumId,
    playingSectionId: state => state.playingSectionId,
    playingSectionData: state => state.playingSectionData
  }),

  created() {
    this.setDocumentTitle();
  },

  mounted() {
    this.clientHeight = document.body.clientHeight;
    if (!this.$route.query.albumId || !this.$route.query.sectionId) {
      return Toast( '链接错误，请联系客服人员' );
    }
    this.receivePlayParams();
    this.getSectionLeaveMessage( this.$route.query.sectionId );
  },

  methods: {
    // todo 设置页面标题为专辑类型
    setDocumentTitle() {
      if (this.isShowTextDesc === true) return document.title = '文稿';
      
      if (this.playingAlbumData) {
        document.title = getAlbumType( this.playingAlbumData.contentType );
      }
    },

    // todo 跳转至全部留言的页面
    toTotalMessagePage() {
      this.$router.push({
        name: 'total-message',
        query: {
          sectionId: this.playingSectionId
        }
      })
    },

    // todo 点击了写留言的区域
    clickWriteMessageBox() {
      if (this.isShowTextDesc === true) {
        this.toSendMessagePage();
      } else {
        this.isShowWriteBox = true;
      }
    },

    // todo 跳转至发表留言的页面
    toSendMessagePage() {
      this.$router.push({
        name: 'send-message',
        query: {
          albumId: this.$route.query.albumId,
          message: '',
          sectionId: this.playingSectionId || this.$route.query.sectionId
        }
      });
    },

    // todo 发送留言的伪处理(不请求接口)
    sendMessage(newMessage) {
      this.message.list.unshift( newMessage );
      if (this.message.list.length > 5) {
        this.message.list.pop();
      }
      this.isShowWriteBox = false;
      this.message.len = this.message.list.length;
    },

    // todo 点赞状态切换的伪处理(不请求接口)
    switchPraise(messageIndex) {
      this.message.list[ messageIndex ].isPraise = !this.message.list[ messageIndex ].isPraise;
      this.message.list[ messageIndex ].praiseUv = this.message.list[ messageIndex ].isPraise ? this.message.list[ messageIndex ].praiseUv + 1 : this.message.list[ messageIndex ].praiseUv - 1;
    },

    // todo 获取条目的 6 条留言
    async getSectionLeaveMessage(sectionId) {
      const sectionMessageResponse = await apiLeaveMessageListBySection(sectionId, this.message.pageNum, this.message.pageSize);
      if (!sectionMessageResponse || sectionMessageResponse.type === 'fail') return Toast('服务器错误,请联系客服');
      let messageDataArr = sectionMessageResponse.data;
      this.message.len = messageDataArr.length;
      if (messageDataArr.length === 6) messageDataArr.pop();
      this.message.list = messageDataArr;
    },

    // todo 切换播放列表排序
    changeListOrder() {
      this.listOrder = !this.listOrder;
      if (this.listOrder === true) {
        this.musicList = this.positiveList;
      } else {
        this.musicList = this.reverseList;
      }
    },

    // todo 构建正反序的播放列表
    buildMusicList() {
      this.positiveList = this.playingAlbumData.items;
      this.musicList = this.playingAlbumData.items;
      this.reverseList = JSON.parse( JSON.stringify( this.positiveList ) ).reverse();
    },

    // todo 接收子组件点击目录事件
    clickMusicList(section, sectionIndex) {
      this.getSectionLeaveMessage(section._id);
    },

    // todo 跳转至专辑介绍页
    clickGetIntoAlbum() {
      const album = this.playingAlbumData;
      toAlbumPage(album.chargeType, album.isBuy, album._id);
    },

    clickPlayOrPauseButton() {
      /*
        todo 1. 如果没有，说明当前没有音频在播放，那么播放当前 query 中的专辑小节
        todo 2. 当前 全局 的 audio 对象有 src 直接判断当前的播放状态，选择切换为播放还是暂停
      */
      if (!this.audio.src) {
        this.checkIsChangeSection(this.albumId, this.sectionId);
      } else {
        this.switchPlayStatus();
      }
    },

    // todo 检测传进来的 albumId 和 sectionId 是否与当前状态中存储的不同，如果不同，那么播放它
    async checkIsChangeSection(albumId, sectionId) {
      if (
        (this.playingSectionId === sectionId && this.playingAlbumId === albumId)
      ) {
        console.log( '未切换音频' );
        return ;
      }
      this.commitSectionDetail( sectionId );

      // ? 防止直接打开页面，状态管理中没有专辑数据
      if (!this.playingAlbumData) {
        await this.getAlbumDetailAndSet( albumId );
        this.playSection( albumId, sectionId );
      } else {
        this.playSection( albumId, sectionId );
      }
    },

    // todo 获取并设置专辑详情到状态管理
    async getAlbumDetailAndSet(albumId) {
      const albumDetailResponse = await apiAlbumDetail(albumId);
      if (!albumDetailResponse || albumDetailResponse.type === 'fail') {
        Toast('专辑错误，请联系客服人员');
        throw new Error(`请求专辑数据失败，id: ${albumId}`);
      }
      this.$store.commit('setPlayingAlbumData', { data: albumDetailResponse.data });
      this.buildMusicList();
    },

    // todo 获取并存储当前小节详情至状态管理中
    async commitSectionDetail(sectionId) {
      const sectionDetail = await this.getSectionDetail(sectionId);
      if (typeof sectionDetail !== 'object') return Toast('本小节音频错误，请联系客服人员');
      this.$store.commit('setPlayingSectionData', { data: sectionDetail });
    },

    // todo 获取小节详情
    async getSectionDetail(sectionId) {
      const sectionDetailResponse = await apiAlbumItemDetail(sectionId);
      if (!sectionDetailResponse || sectionDetailResponse.type === 'fail') return Toast('本小节音频错误，请联系客服人员');
      return sectionDetailResponse.data;
    },

    // todo 接收 query 中的参数
    async receivePlayParams() {
      this.albumId = this.$route.query.albumId;
      this.sectionId = this.$route.query.sectionId;

      // * 如果是第一次进来就不需要立马播放，先去请求专辑和小节的数据
      if (!this.playingAlbumData) {
        this.commitSectionDetail( this.sectionId );
        await this.getAlbumDetailAndSet( this.albumId );
        return ;
      }
      this.buildMusicList();
      this.checkIsChangeSection(this.albumId, this.sectionId);
    },

    // todo 请求数据播放传进来的小节
    async playSection(albumId, sectionId) {
      if (!this.playingAlbumData) {
        
      }
      const section = this.playingAlbumData.items.find(item => {
        return item._id === sectionId;
      });
      const playSource = section.audio;
      this.audio.src = playSource;
      this.switchToPlaying();
      this.savePlayingData(albumId, sectionId);
      this.countSectionPlayTimes(albumId, sectionId);
      this.getSectionLeaveMessage(sectionId);
    },

    // todo 记录播放的专辑id 和 小节id 到状态管理中
    savePlayingData(albumId, sectionId) {
      this.$store.commit('setPlayingAlbumId', { data: albumId });
      this.$store.commit('setPlayingSectionId', { data: sectionId });
    },
    // todo 调用播放接口，统计播放次数
    async countSectionPlayTimes(albumId, sectionId) {
      const countResponse = await apiAlbumItemPlay(albumId, sectionId);
      if (!countResponse || countResponse.type === 'fail') return Toast('音频统计错误，请联系客服人员');
      console.log('countResponse: ', countResponse);
      // 设置页面数据
    },

    countDownFinish() {
      this.countDownTime = 0;
      if (this.audio === null) return ;
      this.$store.commit('audioState/setPlayStatus', { data: false });
      this.audio.pause();
    },

    selectCountDown(item) {
      Toast(`${item.name}后关闭`);
      // this.countDownTime = item.time;
      this.isShowCountDownSheets = false;
    },

    // todo 设置倍速
    selectSpeed(item) {
      this.$store.commit( 'audioState/setAudioPlaybackRate', { data: item.speed } );
      Toast(item.name);
      this.isShowSpeedSheets = false;
    },

    /*
      todo 1. 当前为播放中，调用暂停方法
      todo 2. 当前为暂停中，调用播放方法
    */
    switchPlayStatus() {
      if (!this.playStatus) {
        this.switchToPlaying();
      } else {
        this.switchToPaused();
      }
    },

    // todo 切换播放状态，修改状态管理中的音频播放状态，设置此时用户设置的倍速
    switchToPlaying() {
      this.audio.playbackRate = this.playbackRate;
      this.audio.play();
      this.$store.commit( 'setIsShowFloatWindow', { data: true } );
      this.$store.commit( 'audioState/setPlayStatus', { data: true } );
    },
    switchToPaused() {
      this.audio.pause();
      this.$store.commit( 'audioState/setPlayStatus', { data: false } );
    },

    // todo 展示文稿
    showTextDesc() {
      if (this.playingSectionData.desc === '') return ;
      this.isShowTextDesc = true;
      this.setDocumentTitle();
    },
    closeTextPopup() {
      this.setDocumentTitle();
    },

    showMusicList() {
      this.isShowMusicList = true;
    },

    clickMessageOverlay(e) {
      if (e.clientY > this.clientHeight - 130) return ; // 点击到输入框就不隐藏
      this.isShowWriteBox = false;
    },

    // todo 播放上一曲
    lastMusic() {
      let currPlayIndex = this.findPlayingSectionIndex();
      if (currPlayIndex === -1) return ;

      let sectionListLen = this.playingAlbumData.items.length;
      let lastIndex = currPlayIndex === 0 ? (sectionListLen - 1) : (currPlayIndex - 1);
      this.playSection( this.playingAlbumId, this.playingAlbumData.items[ lastIndex ]._id );
    },
    // todo 播放下一曲
    nextMusic() {
      let currPlayIndex = this.findPlayingSectionIndex();
      if (currPlayIndex === -1) return ;

      let sectionListLen = this.playingAlbumData.items.length;
      let nextIndex = currPlayIndex === (sectionListLen - 1) ? 0 : (currPlayIndex + 1);
      this.playSection( this.playingAlbumId, this.playingAlbumData.items[ nextIndex ]._id );
    },
    // todo 计算当前播放的小节在专辑目录中的索引
    findPlayingSectionIndex() {
      let currPlayIndex = this.playingAlbumData.items.findIndex(item => {
        return item._id === this.playingSectionId;
      });
      return currPlayIndex;
    },
  }
}
</script>

