      border: 1px solid rgba(244,186,65,1);
      border-radius: 17px;
      font-size: 15px;
      color: rgba(244,186,65,1);
    }
  }
  .write-message-box {
    width: 100%;
    position: fixed;
    bottom: 0;
    background-color: #fff;
  }
  .write-message {
    position: absolute;
    bottom: 0;
  }
  .empty-block {
    width: 100%;
    height: 55px;
  }
  .text-desc {
    width: 100%;
    padding: 50px 0 0;

    .title {
      width: calc(100% - 75px);
      margin: 0 37.5px;
      font-size: 18px;
      font-weight: 600;
      color: rgba(51,51,51,1);
    }
    .content {
      width: calc(100% - 75px);
      margin: 0 37.5px;
      margin-top: 40px;
      font-size: 14px;
      color: rgba(102,102,102,1);
      line-height: 2.2;
    }
    
    .middle-block-1 {
      width: 100%;
      height: 10px;
      background:rgba(246,246,246,1);
    }

    .bottom-desc {
      width: 100%;
      height: 55px;
    }
  }

  .music-list-popup-box {
    height: 100%;

    .head-area {
      width: 100%;
      height: 55px;
      box-sizing: border-box;
      padding: 0 20px;
      border-bottom: #EEEEEE solid 1px;

      .title {
        font-size: 16px;
        font-weight: 600;
        color: rgba(51,51,51,1);
      }
      .order {
        width: 45px;
        height: 12px;

        img {
          width: 14px;
          height: 11px;
        }
        p {
          font-size: 13px;
          color: rgba(102,102,102,1);
        }
      }
    }
    .music-list-outer {
      overflow-y: scroll;
      height: calc(100% - 55px);
      
      .music-list-box {
        width: 100%;
        box-sizing: border-box;
        padding: 0 20px;
      }
    }
  }
}
</style>


