<template>
  <div ref="containerProgress" class="container-progress">
    <div class="buttom-floor"></div>
    <div
      class="middle-floor"
      :style="{ 'width': `${trackRate}px` }">
    </div>
    <div
      ref="slider"
      class="top-floor f-c"
      @touchstart="touchStart"
      @touchmove="touchMove"
      @touchend="touchEnd"
      :style="{ 'left': `${trackRate}px` }"
      >
      {{ currentTime | fltTimeToMinute }}/{{ duration | fltTimeToMinute }}
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data() {
    return {
      trackRatio: 0, // 进度条上真实的可以分配比例的部分
      trackRatioStart: 0, // 真实可以分配比例的开始点的 x
      trackRatioEnd: 0, // 真实可以分配比例部分的结束点的 x
      slider: 0, // 滑块宽度
      halfSlider: 0,
      trackWidth: 0, // 进度条宽度
      trackStart: 0, // 进度条起点
      trackEnd: 0, // 进度条终点
      trackRate: 0, // 进度百分比对应的像素
      sideMargin: 0, // 进度条到屏幕边上的边距
    }
  },
  
  computed: mapState({
    currentTime: state => state.audioState.currentTime,
    duration: state => state.audioState.duration,
    audio: state => state.audioState.audio
  }),

  watch: {
    currentTime() {
      this.trackRate = this.currentTime / this.duration * this.trackRatio;
    }
  },

  mounted() {
    this.calculateProgressParams();
  },

  methods: {
    audioIsReady() {
      return this.audio && this.audio.readyState === 4;
    },

    touchStart(e) {
      if ( !this.audioIsReady() ) return ;
      this.audio.pause();
      this.$store.commit( 'audioState/setPlayStatus', { data: false } );
    },

    touchMove(e) {
      if ( !this.audioIsReady() ) return ;
      let x = parseInt(e.changedTouches[0].pageX);
      this.trackRate = x;
      if (x < this.trackRatioStart) {
        this.trackRate = this.trackRatioStart - this.sideMargin - this.halfSlider;
      } else if (x > this.trackRatioEnd) {
        this.trackRate = this.trackRatioEnd - this.sideMargin - this.halfSlider;
      } else {
        this.trackRate = x - this.sideMargin - this.halfSlider;
      }
    },

    touchEnd(e) {
      if ( !this.audioIsReady() ) return ;
      let currentTime = this.trackRate * this.duration / this.trackRatio;
      this.audio.currentTime = currentTime;
      this.audio.play();
      this.$store.commit( 'setIsShowFloatWindow', { data: true } );
      this.$store.commit( 'audioState/setPlayStatus', { data: true } );
    },

    calculateProgressParams() {
      this.trackWidth = this.$refs.containerProgress.clientWidth;
      this.slider = this.$refs.slider.clientWidth;
      this.halfSlider = this.slider >> 1;
      this.sideMargin = document.documentElement.clientWidth - this.trackWidth >> 1;
      this.trackStart = this.sideMargin;
      this.trackEnd = this.trackStart + this.trackWidth;
      this.trackRatio = this.trackWidth - this.slider;
      this.trackRatioStart = this.sideMargin + (this.slider >> 1);
      this.trackRatioEnd = this.trackRatioStart + this.trackRatio;
    }
  }
}
</script>

<style lang="scss" scoped>
.container-progress {
  height: 15px;
  width: 100%;
  position: relative;

  .top-floor {
    width: 80px;
    height: 15px;
    background: rgba(244,186,65,1);
    border-radius: 8px;
    font-size: 11px;
    color: rgba(255,255,255,1);
    position: absolute;
    top: 0;
  }
  .middle-floor {
    width: 50%;
    height: 2px;
    background: rgba(244,186,65,1);
    border-radius: 1px;
    position: absolute;
    top: 50%;
  }
  .buttom-floor {
    width: 100%;
    height: 2px;
    background: rgba(228,233,234,1);
    border-radius: 1px;
    position: absolute;
    top: 50%;
  }
}
</style>


